/* ====================================
		MODULE: NAV
 * ==================================== */
nav {
  padding: 0;
  margin: 0;
  width: 100%;
  box-sizing: border-box; }

.nav {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  width: 100%;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: stretch; }
  .nav-horizontal {
    flex-flow: row wrap; }

.nav-item {
  flex: 1;
  text-align: center;
  padding: 0;
  display: flex;
  flex-flow: column nowrap; }
  .nav-item a {
    flex: 1;
    padding: 0.75rem; }
    .site-nav .nav-item a {
      color: #f9f8f9; }
  .nav-item:hover {
    cursor: pointer; }
    .site-nav .nav-item:hover {
      background-color: #ccbc57; }
    .site-nav .nav-item:hover a {
      color: #0b0a0b; }
  .site-nav .nav-item:active {
    background-color: #e8e1b5; }
  .site-nav .nav-item.active {
    background-color: #ef3c34; }
  .site-nav .nav-item.active a {
    color: #f9f8f9; }

/* ====================================
		MODULE: NAV: SITE-NAV
 * ==================================== */
.site-nav {
  background-color: #630c08; }

/* ====================================
		MODULE: TILE
		tile, panel
 * ==================================== */
/* ====================================
		LAYOUT: GRID AREAS
 * ==================================== */
.site-header {
  grid-area: site-header; }

.site-nav {
  grid-area: site-nav; }

.site-sidebar {
  grid-area: site-sidebar; }

.site-content {
  grid-area: site-content;
  padding: 0;
  margin: 0;
  flex: 1 0 auto;
  display: flex;
  flex-flow: column nowrap;
  justify-content: start;
  align-items: center; }

.site-footer {
  grid-area: site-footer; }

.site-sidebar > .col {
  padding: 0; }

/* ====================================
		LAYOUT: PAGE CONTAINER
 * ==================================== */
#page-container {
  padding: 0;
  margin: 0;
  max-width: 100%;
  width: 100vw;
  min-height: 100vh;
  justify-content: center;
  align-items: space-between;
  display: grid;
  grid-template-rows: repeat(auto-fill, min-content);
  grid-template-areas: "site-header" "site-nav" "site-content" "site-sidebar" "site-footer"; }
  #page-container.site-nav-none {
    grid-template-areas: "site-header" "site-content" "site-sidebar" "site-footer"; }
    #page-container.site-nav-none .site-header + .site-nav {
      display: none; }
  #page-container.sidebar-none {
    grid-template-areas: "site-header" "site-content" "site-footer"; }
    #page-container.sidebar-none.site-sidebar {
      display: none; }

